<template>
  <div id="todo">
    <nav-bar>
      <span v-if="todoCount" slot="center"
        >ÁúãÁùÄÈÇ£‰πàÂ§öÁöÑÂæÖÂäûÊ≤°ÂÅöÔºåÈöæÈÅì‰Ω†üíï‰∏ç‰ºöÁóõÂêó</span
      >
      <span v-else slot="center">Âä†Ê≤πÔºå‰ªäÊó•‰∫ã‰ªäÊó•ÊØï‚ô•</span>
    </nav-bar>
    <task-bar>
      <span slot="tagging" v-if="todoCount"
        >ÁÆó‰∏ãÊù•Ôºå‰Ω†Â∑≤ÁªèÊúâ<span style="color: #ff0000">{{ todoCount }}</span
        >‰ª∂‰∫ãÊãñÁùÄÊ≤°Âπ≤Âï¶</span
      >
      <span slot="tagging" v-else
        >ÂæàÊ£íÔºåÂâçÂá†Â§©Ê≤°ÊúâÁïô‰∏ãÊú™ÂÆåÊàêÂæÖÂäûÔºå‰ªäÂ§©ÁªßÁª≠Âä†Ê≤π</span
      >
      <transition-group name="ani">
        <list-item
          v-for="(item, index) in myNoTodo"
          :item="item.title"
          :done="item.done"
          :key="item.tid"
          @deleteItem="deleteItem(index)"
          @isDone="toDo(index)"
        >
          <span class="done" slot="left">‰ªäÂ§©ÂÆåÊàêÂÆÉ</span>
        </list-item>
      </transition-group>
    </task-bar>
  </div>
</template>

<script>
import NavBar from "components/common/navbar/NavBar.vue";
import TaskBar from "components/content/taskbar/TaskBar.vue";
import ListItem from "components/content/listItem/ListItem.vue";

import {
  getData,
  saveData,
  deleteData,
  getDateStr,
} from "assets/js/todoList.js";

export default {
  name: "Todo",
  components: {
    NavBar,
    TaskBar,
    ListItem,
  },
  data() {
    return {
      myNoTodo: [
        // {title: 'üçéÂêÉËãπÊûú', done: false, createDate: "2021-03-26"},
        // {title: 'üçìÂêÉËçâËéì', done: false, createDate: "2021-03-26"},
        // {title: 'üçåÂêÉÈ¶ôËïâ', done: false, createDate: "2021-03-26"},
        // {title: 'üçàÂêÉÊ©òÂ≠ê', done: false, createDate: "2021-03-26"},
        // {title: 'üçâÂêÉË•øÁìú', done: false, createDate: "2021-03-26"},
      ],
      dateNow: "",
    };
  },
  computed: {
    //Êú™ÂÆåÊàêÊï∞Èáè
    todoCount() {
      return this.myNoTodo.filter((item) => {
        return !item.done;
      }).length;
    },
  },
  // È°µÈù¢Âä†ËΩΩÂç≥Ê∏≤ÊüìÊâÄÊúâÊï∞ÊçÆÂπ∂‰∏îÂæóÂà∞ÂΩìÂâçÊó•Êúü
  created() {
    let allList = getData("myNoTodo");
    this.myNoTodo = allList;
    this.dateNow = getDateStr();
  },
  watch: {
    myNoTodo: {
      handler() {
        saveData(this.myNoTodo, "myNoTodo");
      },
      deep: true, //Ê∑±ÁõëÂê¨
    },
  },
  methods: {
    deleteItem(index) {
      this.myNoTodo.splice(index, 1);
    },
    toDo(index) {
      let myDay = getData("myday");

      this.myNoTodo[index].createDate = this.dateNow;
      myDay.push(this.myNoTodo[index]);
      saveData(myDay, "myday");
      this.myNoTodo.splice(index, 1);
    },
  },
  // ÁªÑ‰ª∂Â§Ñ‰∫éÊ¥ªË∑ÉÁä∂ÊÄÅÊó∂Ë∞ÉÁî®ÁöÑÂáΩÊï∞
  activated() {
    this.myNoTodot = getData("myNoTodo");
  },
};
</script>

